@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
/* Navbar */
.navbar {
    background-color: var(--primary-color);
    color: #fff;
    height: 70px;
}

.navbar ul {
    display: flex;
}

.navbar-logo{
    display: flex;
    align-items: center;
    justify-content: center;
}

.crop {
    margin-top: 2px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
}

.crop img {
    width: 100px;
    -webkit-transform: translate(-35px, -65px);
            transform: translate(-35px, -65px);
}

.navbar h1 {
    font-size: 1.2rem;
}

.navbar a {
    color: #fff;
    padding: 10px;
    margin: 0 5px;
}

.navbar a:hover {
    border-bottom: 2px #fff solid;
}

.navbar .flex {
    justify-content: space-between;
}


@media(max-width: 600px) {
    .navbar {
        height: 110px;
    }

    .navbar .flex {
        flex-direction: column;
    }

    .navbar ul {
        padding: 10px;
        background-color: rgba(0,0,0,0.1);
    }
}

/* Features */

.home-head img, .docs-head img  {
    width: 200px;
    justify-self: flex-end;
}

.home-sub-head img {
    width: 300px;
    justify-self: flex-start;
}

.home-main .card > i {
    margin-right: 20px;
}

.home-main .grid {
    padding: 30px;
}

.home-main .grid > *:first-child {
    grid-column: 1 / span 3;
}

.home-main .grid > *:nth-child(2) {
    grid-column: 1 / span 2;
}

.languages img {
    max-height: 80px;
}

/* Languages */
.languages .flex {
    flex-wrap: wrap;
}
.languages .card {
    text-align: center;
    margin: 18px 10px 40px;

}

.languages .card h4 {
    font-size: 20px;
    margin-bottom: 10px;
}

.languages .card:hover {
    -webkit-animation: flipRight 0.2s;
    animation: flipRight 0.1s forwards;
}

.home-background-list {
    list-style: none;
    margin-left: 0;
    padding-left: 1rem;
}
.home-background-list > li {
    margin: 1rem;
}

.home-sub-head a {
    text-decoration: underline;
}
.home-sub-head a:hover {
    color: #0070f3;
}

.blist-small > li {
    margin-top: 0.1rem;
    margin-bottom: 0.1rem;
}

.home-background-list > li:before {
    display: inline-block;
    content: "-";
    width: 1em;
    margin-left: -1em;
}

@-webkit-keyframes flipRight {
    0% {
        -webkit-transform: scale(1.0) rotate(0deg);
                transform: scale(1.0) rotate(0deg);
    }
    100% {
        -webkit-transform: scale(1.05) rotate(3deg);
                transform: scale(1.05) rotate(3deg);
    }
}

@keyframes flipRight {
    0% {
        -webkit-transform: scale(1.0) rotate(0deg);
                transform: scale(1.0) rotate(0deg);
    }
    100% {
        -webkit-transform: scale(1.05) rotate(3deg);
                transform: scale(1.05) rotate(3deg);
    }
}

@media(max-width: 768px) {
    .home-main .grid {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
    }
    .home-head,
    .home-sub-head,
    .home-sub-head div.container div p {
        text-align: center;
    }
    .home-sub-head img {
        justify-self: center;
    }
}
.spinner {
    -webkit-animation: spin infinite 2s linear;
            animation: spin infinite 2s linear;

    /*You can increase or decrease the timer (5s) to 
     increase or decrease the speed of the spinner*/
  }

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.loading {
    height: 80vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
}

.loading div {
    margin: auto;
}

.left {
    text-align: start;
}

.gh-link:hover {
    text-decoration: underline;
    cursor: pointer;
}

@media(max-width: 768px) {

    .projects-sub-head  {
        text-align: center;
    }
    .projects-sub-head div.container div p {
        text-align: center;
    }
}
/* Utilities */

.container {
    max-width: 1100px;
    margin: 0 auto;
    overflow: auto;
    padding: 0 30px;
}

.grow {
    flex: 1 1;
}

.card {
    background-color: #fff;
    color: #333;
    border-radius: 10px;
    box-shadow: 0 3px 10px rgba(0,0,0,0.2);
    padding: 20px;
    margin: 10px;
}

.btn {
    display: inline-block;
    padding: 10px 30px;
    cursor: pointer;
    background-color: var(--primary-color);
    color: #fff;
    border: none;
    border-radius: 5px;
}

.btn-outline {
    background-color: transparent;
    border: 1px #fff solid;
}

.btn:hover {
    -webkit-transform: scale(.98);
            transform: scale(.98);
}

/* Backgrounds and colored buttons */

.bg-primary, .btn-primary {
    background-color: var(--primary-color);
    color: #fff;
}

.bg-secondary, .btn-secondary {
    background-color: var(--secondary-color);
    color: #fff;
}

.bg-dark, .btn-dark {
    background-color: var(--dark-color);
    color: #fff;
}

.bg-light, .btn-light {
    background-color: var(--light-color);
    color: #333;
}

.bg-primary a, .btn-primary a,
.bg-secondary a, .btn-secondary a,
.bg-dark a, .btn-dark a{
    color: #fff;
}

/* Text colors */

.text-primary {
    color: var(--primary-color);
}

.text-secondary {
    color: var(--secondary-color);
}

.text-dark {
    color: var(--dark-color);
}

.text-light {
    color: var(--light-color);
}

/* Text sizes */
.lead {
    font-size: 20px;
}

.sm {
    font-size: 1rem;
}

.md {
    font-size: 2rem;
}

.lg {
    font-size: 3rem;
}

.xl {
    font-size: 4rem;
}

.text-center {
    text-align: center;
}

/* Alert */
.alert {
    background-color: var(--light-color);
    padding: 10px 20px;
    margin: 15px 0;
}

.alert i {
    margin-right: 10px;
}

.alert-success {
    background-color: var(--success-color);
    color: #fff;
}

.alert-error {
    background-color: var(--error-color);
    color: #fff;
}

.flex {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.align-start{
    justify-content: start;
    align-items: flex-start;
}

.grid{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
    grid-gap: 50px;
    gap: 50px;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

.grid-3 {
    grid-template-columns: repeat(3, 1fr);
}

/* Margin */

.my-1 {
    margin: 1rem 0;
}

.my-2 {
    margin: 1.5rem 0;
}

.my-3 {
    margin: 2rem 0;
}

.my-4 {
    margin: 3rem 0;
}

.my-5 {
    margin: 4rem 0;
}

.m-1 {
    margin: 1rem;
}

.m-2 {
    margin: 1.5rem;
}

.m-3 {
    margin: 2rem;
}

.m-4 {
    margin: 3rem;
}

.m-5 {
    margin: 4rem;
}

/* Padding */

.py-1 {
    padding: 1rem 0;
}

.py-2 {
    padding: 1.5rem 0;
}

.py-3 {
    padding: 2rem 0;
}

.py-4 {
    padding: 3rem 0;
}

.py-5 {
    padding: 4rem 0;
}

.py-6 {
    padding: 5rem 0;
}
.pt-1 {
    padding-top: 1rem;
}

.pt-3 {
    padding-top: 2rem;
}

.pad-bottom-fix {
    padding-bottom: 6rem;
}

.p-1 {
    padding: 1rem;
}

.p-2 {
    padding: 1.5rem;
}

.p-3 {
    padding: 2rem;
}

.p-4 {
    padding: 3rem;
}

.p-5 {
    padding: 4rem;
}
:root {
    --primary-color: #002240;
    --secondary-color: #1c3fa8;
    --dark-color: #002240;
    --light-color: #f4f4f4;
    --success-color: #5cb85c;
    --error-color: #d9534f;
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    font-family: 'Lato', sans-serif;
    color: #333;
    line-height: 1.6;
    overflow-x: hidden;
    overflow-y: scroll;
}

ul {
    list-style-type: none;
}

a {
    text-decoration: none;
    color: #333;
}

h1, h2 {
    font-weight: 300;
    line-height: 1.2;
    margin: 10px 0;
}

p {
    margin: 10px 0;
}

code, pre {
    background: #333;
    color: #fff;
    padding: 10px;
}

/* Scrollbar */
/* width */
::-webkit-scrollbar {
    width: 10px;
    background-color: var(var(--secondary-color));
    border-left: 1px solid #1c3fa8;
    border-left: 1px solid var(--secondary-color);
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
      background-color: #f4f4f4;
      background-color: var(--light-color);
    box-shadow: inset 0 0 5px #1c3fa8;
    box-shadow: inset 0 0 5px var(--secondary-color);
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #002240;
    background: var(--dark-color);
    border-radius: 10px;
    border-left: 1px solid #1c3fa8;
    border-left: 1px solid var(--secondary-color);
  }

/* Stats */

.stats {
    padding-top: 100px;
    -webkit-animation: slideInFromBottom 1s ease-in;
            animation: slideInFromBottom 1s ease-in;
}

.stats-heading {
    max-width: 500px;
    margin: auto;
}

.stats .grid h3 {
    font-size: 35px;
}

.stats .grid p {
    font-size: 20px;
    font-weight: bold;
}

/* CLI */
.cli .grid {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
}

.cli .grid > *:first-child {
    grid-column: 1 / span 2;
    grid-row: 1 / span 2;
}

/* Cloud */
.cloud .grid {
    grid-template-columns: 4fr 3fr;
}

/* Docs */
.docs-main h3 {
    margin: 20px 0;
}

.docs-main .grid {
    grid-template-columns: 1fr 2fr;
    align-items: flex-start;
}

.docs-main nav li {
    font-size: 17px;
    padding-bottom: 5px;
    margin-bottom: 5px;
    border-bottom: 1px #ccc solid;
}

.docs-main a:hover {
    font-weight: bold;
}


/* Animations */
@-webkit-keyframes slideInFromLeft {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}
@keyframes slideInFromLeft {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@-webkit-keyframes slideInFromRight {
    0% {
        opacity: 0;
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@keyframes slideInFromRight {
    0% {
        opacity: 0;
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@-webkit-keyframes slideInFromTop {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes slideInFromTop {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@-webkit-keyframes slideInFromBottom {
    0% {
        opacity: 0;
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes slideInFromBottom {
    0% {
        opacity: 0;
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

/* Tablets and under */

@media(max-width: 768px) {
    .grid,
    .showcase .grid,
    .stats .grid,
    .cli .grid,
    .cloud .grid,
    .features-main .grid,
    .docs-main .grid {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
    }

    .showcase {
        height: auto;
    }

    .showcase-text {
        text-align: center;
        margin-top: 40px;
        -webkit-animation: slideInFromTop 1s ease-in;
                animation: slideInFromTop 1s ease-in;
    }

    .showcase-form {
        justify-self: center;
        margin: auto;
        -webkit-animation: slideInFromBottom 1s ease-in;
                animation: slideInFromBottom 1s ease-in;
    }
    
    .cli .grid > *:first-child {
        grid-column: 1;
        grid-row: 1;
    }

    .features-head,
    .features-sub-head,
    .docs-head {
        text-align: center;
    }

    .features-head img,
    .features-sub-head img,
    .docs-head img {
        justify-self: center;
    }

    .features-main .grid > *:first-child,
    .features-main .grid > *:nth-child(2) {
        grid-column: 1;
    }
}

/* Mobile */

@media(max-width: 600px) {
    .navbar {
        height: 110px;
    }

    .navbar .flex {
        flex-direction: column;
    }

    .navbar ul {
        padding: 10px;
        background-color: rgba(0,0,0,0.1);
    }
    .showcase-form {
      width: 300px;
    }
}


/* Footer */

.footer {
    position: fixed;
    bottom: 0;
    width: 100%;
}

.footer .container {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    width: 100%;
    flex-wrap: wrap
}

.copyright h1 {
    font-size: 1rem;
}
.copyright p {
    font-size: 0.8rem;
}

.social {
    align-self: center;
    display: flex;
    justify-content: flex-end;
}

.social a {
    margin: 0 10px;
}

@media (max-width:760px) {
    .footer{
        position: relative;
        display: flex;
        padding-bottom: 5px;
    }

    .copyright h1,
    .copyright p {
        text-align: center;
    }
}

/* Showcase */

.contact {
    height: 400px;
    background-color: var(--primary-color);
    color: #fff;
    position: relative;
    margin-bottom: 100px;
}
.contact h1 {
    font-size: 40px;
}

.contact p {
    margin: 20px 0;
}

.contact .grid {
    grid-template-columns: 55% 45%;
    grid-gap: 30px;
    gap: 30px;
    overflow: visible;
}

.contact-text {
    -webkit-animation: slideInFromLeft 1s ease-in;
            animation: slideInFromLeft 1s ease-in;
}

.contact-form {
    position: relative;
    top: 60px;
    height: 450px;
    width: 400px;
    padding: 40px;
    z-index: 100;
    justify-self: flex-end;
    -webkit-animation: slideInFromRight 1s ease-in;
            animation: slideInFromRight 1s ease-in;
}

.contact-form .form-control {
    margin: 30px 0;
}

.contact-form input[type='text'],
.contact-form input[type='email'],
.contact-form textarea {
    border: 0;
    border-bottom: 1px solid #b4becb;
    width: 100%;
    padding: 3px;
    font-size: 16px;
    resize: none;
    font-family: 'Lato';
}

.contact-form textarea {
    height: 80px;
}

.contact-form input:focus {
    outline: none;
}



@media(max-width: 1000px) {
    .contact .grid {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
    }
    .contact {
        height: auto;
    }

    .contact-text {
        text-align: center;
        margin-top: 40px;
        -webkit-animation: slideInFromTop 1s ease-in;
                animation: slideInFromTop 1s ease-in;
    }

    .contact-form {
        top: 20px;
        justify-self: center;
        margin: auto;
        -webkit-animation: slideInFromBottom 1s ease-in;
                animation: slideInFromBottom 1s ease-in;
    }
    
}

